@import "https://fonts.googleapis.com/css?family=Open+Sans:400,700|Space+Mono:400,700";
.page-icon-container .page-bg-img {
  display: inline-block;
  width: 100%;
  height: auto;
  stroke-linecap: round;
}

.page-icon-container {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  max-width: 300px;
  width: 55%;
  height: auto;
  margin: 0;
  padding: 0;
  position: relative;
  background: none;
}
.page-icon-container.portfolio .page-bg-img {
  stroke: #32C8EB;
  stroke-width: 2;
  fill: #32C8EB;
}
.page-icon-container.portfolio .code-icon {
  stroke: black;
  stroke-width: 3;
  fill: none;
}

.page-icon-container.experiments .page-bg-img {
  stroke: #8EA6FC;
  stroke-width: 2;
  fill: #2C3037;
}
.page-icon-container.experiments .flask-icon {
  stroke: black;
  stroke-width: 3;
  fill: none;
}
.page-icon-container.experiments .flask-icon.measure {
  stroke: black;
  stroke-width: 1;
  fill: none;
}
.page-icon-container.experiments .flask-icon.solution-top {
  stroke: #8EA6FC;
  stroke-width: 3;
  fill: #8EA6FC;
}
.page-icon-container.experiments .flask-icon.bottom {
  stroke: black;
  stroke-width: 3;
  fill: #7C93E0;
}
.page-icon-container.experiments .flask-icon.bubble {
  stroke: #25282E;
  stroke-width: 0.9;
  fill: #8EA6FC;
}

.page-icon-container.about .page-bg-img {
  stroke: #25282E;
  stroke-width: 2;
  fill: #25282E;
}
.page-icon-container.about .user-icon {
  stroke: black;
  stroke-width: 3;
  fill: #8EA6FC;
  -webkit-transform: scale(0.75) translate(42%, -1%);
      -ms-transform: scale(0.75) translate(42%, -1%);
          transform: scale(0.75) translate(42%, -1%);
}

.page-icon-container.contact .page-bg-img {
  stroke: #8EA6FC;
  stroke-width: 2;
  fill: #8EA6FC;
}
.page-icon-container.contact .chat-icon {
  stroke: #25282E;
  stroke-width: 3;
  fill: #7C93E0;
  -webkit-transform: scale(0.8) translateX(40%);
      -ms-transform: scale(0.8) translateX(40%);
          transform: scale(0.8) translateX(40%);
}
.page-icon-container.contact .chat-icon.text {
  stroke-width: 3.5;
}

.icons {
  stroke-linejoin: round;
  stroke-linecap: round;
  -webkit-transform: scale(0.8) translateX(38%);
      -ms-transform: scale(0.8) translateX(38%);
          transform: scale(0.8) translateX(38%);
}

.home-icon.roof {
  stroke-width: 5;
}



